.liquify-table {
  font-family: "Carnas-Regular";
  font-size: 14px;
}
.liquify-table .thead-dark-ui th {
  background-color: #f3f3f3;
  color: #414042;
}
.liquify-table tbody th,
.liquify-table tbody td {
  font-weight: normal;
}

.lq {
  display: inline-block;
  height: 14px;
  width: 14px;
}
.lq-done {
  background: url("../../assets/images/done.png") 0 0px;
  background-repeat: no-repeat;
}
.lq-in-progress {
  background: url("../../assets/images/in-progress.png") 0 0;
  background-repeat: no-repeat;
}
.lq-fail {
  background: url("../../assets/images/fail.png") 0 0;
  background-repeat: no-repeat;
}
.lq-status {
  text-transform: lowercase;
  display: inline-block;
  margin-left: 5px;
}
.lq-status::first-letter {
  text-transform: uppercase;
}
