.lq-switch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 20px;
}

.lq-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.lq-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.lq-slider:before {
  position: absolute;
  content: "";
  height: 12px;
  width: 12px;
  left: 3px;
  bottom: 3px;
  background-color: #b5b5b5;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .lq-slider {
  background-color: #fff;
}

input:focus + .lq-slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .lq-slider:before {
  background-color: #69b37b;
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.lq-slider.lq-round {
  border-radius: 34px;
}

.lq-slider.lq-round:before {
  border-radius: 50%;
}
.cb-lq-label {
  margin-right: 10px;
  display: inline-block;
}
