.modal-lg {
  max-width: 900px;
}
.modal-header,
.modal-footer {
  border: 0;
}
.modal-header {
  padding: 30px 30px 15px;
}
.modal-title {
  font-size: 24px;
  color: #414042;
  font-weight: 500;
}
.modal-body {
  padding-top: 0;
  padding-bottom: 30px;
  color: #414042;
}
.modal-body .lq-modal-body {
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
  margin: 0 20px;
  padding: 0 15px 15px 15px;
  box-sizing: border-box;
}
.modal-body .lq-modal-body .m-accounts {
  list-style: none;
  margin-top: 15px;
  padding: 0 0 0 20px;
}
.modal-body .lq-modal-body .m-accounts .lq-list {
  border-left: 1px solid#fafafa;
  list-style: none;
  padding: 10px 20px;
  border-bottom: 1px solid#ececec;
  cursor: pointer;
}
.modal-body .lq-modal-body .lq-list span {
  display: block;
  font-size: 14px;
}
.modal-body .lq-modal-body .lq-list.lq-active {
  background: #fff;
  border-left: 1px solid#f8df2f;
}
.modal-body .lq-modal-body .lq-list .lq-list-title {
  font-family: "Carnas-Light";
  color: #989898;
}
.modal-body .lq-modal-body .lq-list .lq-list-account {
  font-family: "Carnas-Medium";
  font-size: 18px;
}
.modal-footer {
  border-top: 1px solid #ececec;
  padding: 15px 0 0 0;
}
.modal-footer .lq-modal-footer {
  position: static;
}
.modal-footer .lq-modal-footer .lg-close {
  position: absolute;
  left: 15px;
}
.lq-modal-left {
  background-color: #fafafa;
}
.lq-modal-right {
  padding: 15px 0;
  min-height: 320px;
}
.lq-modal-right .lq-sub-heading {
  font-size: 18px;
  margin-bottom: 20px;
}
.lq-list .lq-list-flag .flag {
  display: inline-block;
  width: 20px;
  height: 13px;
  margin-right: 5px;
  background-repeat: no-repeat;
}
.lq-list .lq-list-flag .GBP {
  background: url("../../assets/images/gbp.png") 0 0;
  background-size: cover;
}
.lq-list .lq-list-flag .EUR {
  background: url("../../assets/images/eur.png") 0 0;
  background-size: cover;
}
.lq-list .lq-list-flag .USD {
  background: url("../../assets/images/usd.png") 0 0;
  background-size: cover;
}
.lq-list .lq-list-flag .HKD {
  background: url("../../assets/images/hkd.png") 0 0;
  background-size: cover;
}
.lq-list .lq-list-flag .AED {
  background: url("../../assets/images/uae.gif") 0 0;
  background-size: cover;
}
.lq-list .lq-list-flag .JPY {
  background: url("../../assets/images/jpy.gif") 0 0;
  background-size: cover;
}
.lq-ckb {
  float: right;
  font-size: 14px;
}
