.input-form-control-wrapper {
  position: relative;
  display: block;
  margin-bottom: 0;
}
.input-form-input {
  padding: 36px 20px 28px 20px;
  font-size: 14px;
}
.input-form-control-lable {
  position: absolute;
  top: 20px;
  left: 20px;
  transition: all 0.3s linear;
}
.input-form-control-lable-hidden {
  top: 22px;
}
.input-form-control-lable-active {
  font-size: 14px;
  top: 5px;
  color: #ababab;
}
.lq-select {
  color: #414042;
}
.lq-select-wrapper {
  font-size: 14px;
  position: relative;
}
.lq-select-wrapper label {
  position: absolute;
  top: 5px;
  left: 10px;
  color: #ababab;
  position: relative;
  z-index: 1;
}
.lq-custom-select {
  width: 100%;
  padding: 25px 20px 15px 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; /* remove default arrow */
  background-image: url(../../assets/images/chevron_bottom.png); /* add custom arrow */
  background-repeat: no-repeat;
  background-position: right 10px center;
  overflow: hidden;
  position: absolute;
  left: 0;
}
