.liquify-dashboard .left-block {
  padding-top: 25px;
  padding-bottom: 25px;
}
.liquify-dashboard .right-block {
  padding-top: 25px;
  padding-left: 40px;
  padding-bottom: 25px;
  box-shadow: -8px 0 8px 0 rgba(0, 0, 0, 0.06);
  background-color: #ffffff;
}
.liquify-dashboard .section-title {
  color: #414042;
  font-size: 20px;
  margin-bottom: 10px;
}
.liquify-dashboard .section-title .filter {
  color: #414042;
  font-size: 14px;
  font-style: normal;
  margin-right: 10px;
}
.liquify-dashboard .section-title .lg-filter {
  display: inline-block;
  background: url("../../assets/images/filter.png") center right;
  background-repeat: no-repeat;
  padding-right: 20px;
  cursor: pointer;
}
.liquify-dashboard .transaction-btn {
  border: solid 1px #cacaca;
  background-color: #fff;
  color: #414042;
}
.liquify-dashboard .no-records {
  color: #ca0000;
  font-size: 14px;
}
